.home-contain{
    
    width: 100%;
    height: 10px;
    position: sticky;
    top: 0;
    z-index: 3000;
}
.back-home{
    position: absolute;
    top: 0;
    right: 0;
    background-color: #B5E5CF;
    border: none;
    border-radius: 50%;
    z-index: 2000;
    cursor: pointer;
    width: 80px;
    height: 80px;
}
.back-home>svg{
    width: 70%;
    height: 70%;
}


