.art-page{
    min-height: 100vh;
    padding-bottom: 10%;
    scroll-snap-align: start;
}
.art-head{
    text-align: center;
}
.art-source{
    color: coral;
    font-size: 1.2rem;
    display: inline-block;
    padding: 0 .5rem;
    margin-top: 1rem;
    font-weight: 600;
    position: relative;
    border-left: solid coral 3px;
    border-right: solid coral 3px;
}
.art-content{
    font-size: 1.2rem;
}