header{
    height: 100vh;
    scroll-snap-align: start;
    position: relative;
}
.head-section{
    height: 100vh;
    clip: rect(0, auto, auto, 0);
    overflow: hidden;
    position: relative;
    left: 0;
    top: 0;
    width: 100%;
}
.main-head{
    background-color: #B5E5CF;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)
}

.form-head{
    position: relative;
    flex: 1;
}
.title{
    font-size: 2.6rem;
    text-transform: uppercase;
    position: absolute;
    white-space: nowrap;
    left: -6rem;
}
.title::after{
    content: '';
    height: 8px;
    width: 120px;
    background-color: coral;
    position: absolute;
    bottom: -10px;
    left: 2rem;
}

@media (max-width:1100px) {
    .main-head{
        flex-direction: column;
    }
    .form-head{
        text-align: center;
        position: static;        
        flex-basis: 4rem;
    }
    .title{
        font-size: 2.5rem;
        position: absolute;
        left: 2rem;
    }
}

.form-main{
    flex-grow: 1.5;
    padding: 3rem;
    gap: 1rem;
    text-align: left;
}
.form-el{
    gap: .5rem;
}
.form-el>label{
    font-size: 1.3rem;
    font-weight: 500;
}
.form-el>input{
    height: 2rem;
    padding: 0 1rem;
}
.form-el>textarea{
    resize: none;
    height: 10rem;
    padding: 1rem;
}
.form-el>textarea,
.form-el>input,
.form-el>textarea:hover,
.form-el>input:hover,
.form-el>textarea:focus,
.form-el>input:focus{
    border: none;
    outline: none;
}
.form-main>button{
    cursor: pointer;
    text-transform: uppercase;
    font-size: 1.5rem;
    font-weight: 500;
    border: none;
    background-color: coral;
    color: white;
    padding: .5rem 0;
}