.arts-section{
    height: 100vh;
    background-color: #F1E6D6;
    margin-bottom: 50px;
    padding: 50px; 
    position: relative;
    overflow: scroll;
    scroll-snap-align: center;
}
.arts-container{
    width: 100%;
    max-width: 1300px;
    margin: auto;
}

.arts-title{
    text-align: center;
    padding: 2rem 0;
    font-size: 3rem;
    position: relative;
    border-top: #006D5B solid .8rem;
    border-bottom: #006D5B solid .8rem;
    margin-bottom: 3rem;
}
.arts-title::after,
.arts-title::before{
    content: '';
    background-color: #006D5B;
    height: 1px;
    width: 100%;
    position: absolute;
    left: 0;
}
.arts-title::after{
    top: .7rem;
}

.arts-title::before{
    bottom: .7rem;
}

.arts{
    
    display: inline-block;
    width: 100%;
    padding: 20px 35px;
    border-radius: 5px;
    border: dotted 1px #006D5B;
    position: relative;
}
.art-title{
    font-size: 1.7rem;
    margin: auto;
   
    position: relative;
    
}
.art-title::after,
.art-title::before{
    content: '';
    height: 1px;
    width: 100%;
    position: absolute;
    left:0;
}
.art-title::after{
    bottom: -10px;
}
.art-title::before{
    bottom: -10px;
}

.my-masonry-grid {
    display: -webkit-box; 
    display: -ms-flexbox; 
    display: flex;
    margin-left: -30px; 
    width: auto;
}
.my-masonry-grid_column {
    padding-left: 30px; 
    background-clip: padding-box;
}
  

.my-masonry-grid_column > div { 
    margin-bottom: 30px;
}
.close{
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    background: #006D5B;
    border: none;
    font-size: 1rem;
    cursor: pointer;
    color: white;

    position: absolute;
    right: .5rem;
    top: .5rem;
    z-index: 1000;
}
